<template>
  <div>
    <!-- 头部分类 -->
    <div class="groups">
      <div class="titles">
        <div>标签分类：</div>
        <div>
          <el-input v-model="searchName" placeholder="输入回车快捷搜索" @keyup.enter.native="searchRootLevel" />
        </div>
      </div>
      <!-- 一级目录 -->
      <div class="menus-list">
        <span
          v-for="item in showMenus"
          :key="item.key"
          @click="chooseMenus(item)"
          :class="{
            'active-groups': chooseTypes === item.name
          }"
        >
          {{ item.meta.title }}
        </span>
      </div>
      <!-- 二级目录 -->
      <div class="books-list">
        <div
          v-for="item in booksList"
          :key="item.name"
          @click="chooseBooks(item)"
        >
          {{ item.meta.title }}
        </div>
      </div>
      <!-- 书籍内容 -->
      <el-tabs
        v-model="editableTabsValue"
        type="card"
        closable
        @tab-remove="removeTab"
      >
        <el-tab-pane
          v-for="(item, index) in editableTabs"
          :key="index"
          :label="item.meta.title"
          :name="item.name"
        >
          <component :is="item.component" />
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script>
import webFront from './webFront/menus.js'
import node from './node/menus.js'
import markDown from './markDown/menus.js'
const topPush = 10 // 按模块来说推荐前10个到首页
const typeMenus = [webFront, node, markDown]
export default {
  data () {
    return {
      searchName: '',
      chooseTypes: typeMenus[0].name,
      showMenus: [...typeMenus],
      editableTabsValue: typeMenus[0].children[0].name,
      editableTabs: [typeMenus[0].children[0]]
    }
  },
  computed: {
    topBooks () {
      return typeMenus.reduce((arr, cur) => {
        const list = [...cur.children]
        return arr.concat(list.splice(0, topPush) || [])
      }, [])
    },
    booksList () {
      return this.chooseTypes === '' ? this.topBooks : typeMenus.find(el => el.name === this.chooseTypes)?.children || []
    }
  },
  methods: {
    chooseMenus ({ name }) {
      this.chooseTypes = name
    },
    searchRootLevel () {
      return this.showMenus = typeMenus.filter(el => el.meta.title.includes(this.searchName))
    },
    removeTab (targetName) {
      if (this.editableTabs.length > 1) {
        this.editableTabs = this.editableTabs.filter(el => el.name !== targetName)
        this.editableTabsValue = this.editableTabs[this.editableTabs.length - 1].name
      }
    },
    // 切换，选中书籍
    chooseBooks (rowData) {
      const { name } = rowData
      if (!this.editableTabs.some(el => el.name === name)) {
        this.editableTabs.push(rowData)
      }
      this.editableTabsValue = name
    }
  }
}
</script>
<style lang="less" scoped>
img {
  width: 100%;
  height: 300px;
  border-radius: 4px;
}
.groups {
  margin-top: 12px;
  .titles {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .menus-list {
    font-size: 14px;
    margin: 8px 12px;
    span{
      cursor: pointer;
      display: inline-block;
      line-height: 22px;
      &+span {
        margin-left: 5px;
      }
      &:hover {
        text-decoration: underline;
        font-size: 15px;
      }
      &.active-groups {
        color: #409EFF;
      }
    }
  }
}
.books-list {
  border: 1px solid #8c8c8c;
  border-radius: 4px;
  padding: 8px;
  margin-bottom: 12px;
  >div {
    display: inline-block;
    cursor: pointer;
    &+div {
      margin: 0 8px;
    }
    &:hover {
      text-decoration: underline;
    }
  }
}
</style>
